@import "sanitize";

$grey: #808080;
$blue: #38A6F9;
// $text-color: #5c5c5c;
$text-color: #000;
$bg-color: #f8f8f5;

body {
  font-family: "Avenir Next", "Segoe UI", "Helvetica Neue", Arial, "Hiragino Sans GB", "PingFang SC", "Heiti SC", "Microsoft YaHei UI", "Microsoft YaHei", "Source Han Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  color: $text-color;
}

body:hover {
  .header-bg {
    transform: scale(1.6);
  }
}

.fonts-loaded {
  body .title {
    font-family: 'Open Sans', sans-serif;
  }
}

.header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  text-align: center;
  overflow: hidden;
  color: #fff;
  background-color: #fff;

  .header-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../images/background.jpeg');
    background-size: cover;
    background-position: center center;
    transition: transform 36s;
    transition-timing-function: linear;
  }

  .caption {
    z-index: 10;
  }

  #getting-started {
    font-size: 62px;
    font-weight: lighter;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  }

  .title {
    font-size: 42px;
    font-weight: lighter;
    letter-spacing: 0.2em;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  }

  .post-list {
    margin-top: 2em;
    color: #fff;

    li {
      margin-bottom: 1em;

      a {
        position: relative;
        color: #fff;
        text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);

        &:after {
          position: absolute;
          bottom: 0;
          left: 0;
          content: "";
          display: inline-block;
          height: 1px;
          width: 100%;
          background: #fff;
          transform: scaleX(0);
          transition: transform 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
        }

        &:hover {
          &:after {
            transform: scaleX(1);
          }
        }
      }
    }
  }

  footer {
    position: absolute;
    bottom: 1em;
    left: 50%;
    margin-left: -39px;
    font-size: 12px;
  }
}
